* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Kalameh', 'Helvetica Neue', Helvetica, Arial;
}

.form-row {
    display: flex;
    align-items: center;
    gap: 5px;
    padding: 10px;
}
input {
    width: 80px;
    height: 35px;
    cursor: pointer;
}
.add-color {
    padding: 8px 17px;
    height: 35px;
    background-color: rgb(22, 192, 87);
    color: #fff;
    border: 0;
    border-radius: 3px;
    cursor: pointer;
}
.add-color:hover {
    background-color: rgb(17, 155, 70);
}
.colors-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 13px;
    list-style-type: none;
    padding: 30px 15px;
}
.color {
    text-shadow: 0 0 0 #000;
    padding: 5px 13px;
    border-radius: 3px;
    cursor: pointer;
    box-shadow: 0 2px 4px rgba(145, 145, 145, 0.342);
}
.color:hover {
    opacity: .85;
}